    // 点击删除流程的确定按钮成功并提示
    correctremovedel() {
      this.delmessager =this.delmessager.replace(/\s+/g,"")
      if (this.delmessager == "") {
        this.$message.warning("请输入删除申请意见");
        return;
      }
      this.showremove = false;
      this.applyReason = this.delmessager;
      this.delmessager = "";
      this.$axios
        .post("/api/requestDeleteProcessInstances", {
          procInstIds: this.multipleSelection[0].procInstID,
          applyReason: this.applyReason
        })
        .then(res => {
          let data = eval("(" + res.data.result + ")");
          if (data.Status === "0") {
            this.$message.warning(`${data.Message}`);
            //如果删除成功，再从新刷新列表 6不能删
            this.getlist();
          } else if (data.Status === "6") {
            this.$message.success(`${data.Message}`);
            this.$refs.multipleTable.clearSelection();
            this.getlist();
          } else {
            this.$message.error(`${data.Message}`);
          }
        })
        .catch(err => {
          this.$message.error(`删除失败！${err}`);
        });
    },
