    openDailog(type, userType) {
      //控制弹窗头部标题显示隐藏(默认显示)
      this.componentParam.closeShow = false;
      // 弹出框类型
      if (type == "originator_dailog") {
        //发起人
        this.childDataUrl.getTree = "getTree";
        this.childDataUrl.companytree = "companytree/page";
        if (userType == "launch") {
          util.burialPoint('查询流程-输入条件-发起人输入框');
          //发起人
          this.userType = userType;
          this.oneCheck = false;
          this.childDataUrl.selectedPerson = this.launchUser;
          this.queryName = this.launchqueryName;
        } else if (userType == "examine") {
          util.burialPoint('查询流程-输入条件-审批人');
          //审批人
          this.oneCheck = true;
          this.userType = userType;
          this.childDataUrl.selectedPerson = this.examineUser;
          this.queryName = this.examinequeryName;
        }
      } else if (type == "processSelection") {
        util.burialPoint('查询流程-输入条件-流程类别');
        //新流程选择
        // 弹出框类型
        this.componentParam.componentType = type;
        //是否单选
        this.componentParam.oneCheck = true;
      }
      this.comp.component = type;
      this.showdialog = true;
      util.showDailog_mask(); //解决ie浏览器问题显示遮罩
    },
    /**
     * 弹窗组件带出数据赋值给主组件
     */
    showMsgfromchild(data) {
      var that = this;
      if (data.type && data.type == "originator") {
        if (that.userType == "launch") {
          //发起人
          this.launchUser = [];
          this.oneCheck = false;
          this.model.userName = [];
          for (let origina of data.peoArr) {
            var userId = origina.userId;
            var vankeId = origina.vankeId;
            var bool = false;
            for (var j = 0; j < this.launchUser.length; j++) {
              if (this.launchUser[j].userId === userId) {
                bool = true;
              }
            }
            if (!bool) {
              this.launchUser.push({
                userId: userId,
                value: origina.name,
                vankeId: vankeId
              });
              this.model.userName.push(origina.name);
            }
          }
        } else if (that.userType == "examine") {
          //审批人
          this.examineUser = [];
          this.oneCheck = true;
          this.model.managerName = [];
          for (let origina of data.peoArr) {
            var userId = origina.userId;
            var vankeId = origina.vankeId;
            var bool = false;
            for (var j = 0; j < this.examineUser.length; j++) {
              if (this.examineUser[j].userId === userId) {
                bool = true;
              }
            }
            if (!bool) {
              this.examineUser.push({
                userId: userId,
                value: origina.name,
                vankeId: vankeId
              });
              this.model.managerName.push(origina.name);
            }
          }
        }
        //end
      } else if (data.type && data.type == "processSelection") {
        //选择流程类别
        this.componentParam.backfillData = data.list;
        let processId = [];
        let processName = [];
        for (let node of data.list) {
          processName.push(node.name);
          processId.push(node.processId + "");
        }
        this.model.selectForm.processFullName = processId.join(",");
        this.processFormName = processName.join("；");
      }
      this.hideComponent();
    },
    //点击关闭弹窗
    hideComponent() {
      this.showdialog = false;
      util.hiddenDailog_mask(); //解决ie浏览器问题 隐藏遮罩
    },
    //搜索
    search() {
      util.burialPoint('查询流程-输入条件-搜索按钮');
      this.pageShow = true;
      this.pageIndex = 1;
      this.getlist();
