  },
  /*
    * methods end
    * */

  //    计算方法
  watch: {
    "model.userName": {
      handler: function(n, o) {
        let ele = this.$refs.userId;
        util.inputHeight(n, ele);
        this.dataTitle = "";
        // 监听数组list 的第一项的name值变化；n 为新数据，o 为旧数据
        for (var i = 0; i < n.length; i++) {
          if (typeof n[i] == "object") {
            this.dataTitle += n[i].value + ",";
          } else if (typeof n[i] == "string") {
            this.dataTitle += n[i] + ",";
          }
        }
        this.dataTitle = this.dataTitle.substr(0, this.dataTitle.length - 1);
      },
      deep: true
    },
    "model.date":{
      handler: function(n, o) {
        this.startDate = false;
        this.$nextTick(() => {
          this.startDate = true
        });
        this.endDate = false;
        this.$nextTick(() => {
          this.endDate = true
        })
      },
      deep: true
    },
     Visible:{
      handler(n, o) {
        if(n){
          switch (this.typeTab){
            case 'User':{
              this.echoData = [...this.serachSlectData.tagsOwnsUser];
              break
            }
            case 'Org':{
              this.echoData = [...this.serachSlectData.tagsOrgSelect];
              break
            }
          }
          switch (this.ProcessType){
            case 'Process':{
              this.echoData = [...this.serachSlectData.tagsOwnProcess];
              break
            }
            case 'Category':{
              this.echoData = [...this.serachSlectData.tagsOwnCategory];
              break
            }
            case 'CurrentUser':{
              this.echoData = [...this.serachSlectData.CurrentUser];
              break
            }
            case 'ToWrite':{
              this.echoData = [...this.serachSlectData.ToWriteData];
              break
            }
          }
        }
      }
    },
  },
  computed: {
    bodyHeight() {
      return document.documentElement.clientHeight
    },
    ...mapGetters(["isIE", "isSafari", "bodyHeight"])
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
  .list{
    a{
      cursor: pointer;
    }
    a:hover{
      text-decoration: underline;
      color: #00a1f1;
    }
  }
</style>



